/* DEFAULT */
html {
  font-size: 18px;
  font-family: "Avenir", "Helvetica Neue", sans-serif;
  line-height: 1.5;
  background-color: #fafafa;
  color: #222222;
}

body {
  margin: 0;
}

/* HEADER */

header {
  padding: 2em 4% 0 4%;
}

/* NAV comunes*/

/* donar */
#donarHeader {
  /*14.4 px*/
  font-size: 0.8em;
  padding: 0.5em;
  width: auto;
  background-color: rgb(0, 51, 51);
  color: #fafafa;
  border-radius: 0.5em;
  transition: background-color 0.5s ease;
  transition: color 0.5s ease;
}

#donarHeader:hover {
  /*14.4 px*/
  background-color: #cccccc;
  color: rgb(0, 51, 51);
}

/* idiomas */
#langActive {
  color: black;
  font-weight: 700;
}

.langInactive:hover {
  color: rgb(0, 51, 51) !important;
}

.langInactive,
.langInactive:visited,
.langInactive:focus {
  transition: color 0.5s ease;
  color: grey;
}

/* título */
.titlePage {
  padding: 30px 0px 10px 0px;
  text-align: center;
}

/* NAVEGACIÓN */
/* marca W */
/* accesibilidad, saltar menú e ir al contenido */
.contentGo {
  display: none;
}

.marca {
  width: 200px;
  margin: auto;
  display: block;
  clear: both;
  padding-top: 1em;
}

/* menu principal visible*/
nav ul {
  /* 22px */
  font-size: 1.222em;
  font-weight: 600;
  text-align: center;
  margin: 0;
  padding: 0;
}

nav ul li {
  padding: 1em 1% 0 1%;
  display: inline-block;
}

nav ul li a {
  text-decoration: none;
  color: rgb(0, 51, 51);
}

.activeMenu {
  border-bottom: 2px solid rgb(0, 51, 51);
}

.inactiveMenu {
  color: #aaaaaa;
  border-bottom: 2px solid #aaaaaa;
  transition: border-bottom 0.5s ease;
}

.inactiveMenu:hover {
  border-bottom: 2px solid rgb(0, 51, 51);
}

/* menu principal responsive oculto */
.iconMenu {
  font-size: 2em;
  transition: color 0.5s ease;
}

.iconMenu:hover {
  color: #aaaaaa;
}

#navRes {
  display: none;
}

.menuRes li ul.dropdown {
  display: none;
  width: 50%;
  padding-top: 1em;
  padding-bottom: 1em;
  position: absolute;
  left: 25%;
  z-index: 999;
  background: white;
}

.menuRes li ul.dropdown li {
  width: 100%;
  display: block;
  padding: 1em 0 1em 0;
  font-size: 0.8em;
}

.dropdown li a {
  font-size: 0.8em;
}

/* menu migas de pan - breadcrumb & menú dropdown */
.breadcrumb {
  width: 100%;
  float: left;
  clear: both;
  margin-bottom: 1em;
}

.breadcrumb li {
  display: inline-block;
  position: relative;
  line-height: 21px;
  text-align: left;
  font-size: 0.8em;
}

.breadcrumb li a {
  display: block;
  padding: 1em 1.5em;
  color: #aaaaaa;
  text-decoration: none;
}

.breadcrumb li a:hover {
  color: rgb(0, 51, 51) !important;
}

.breadcrumb li ul.dropdown {
  width: 70%;
  /* Set width of the dropdown */
  background: #f2f2f2;
  display: none;
  position: absolute;
  z-index: 999;
  left: 1.25em;
}

.breadcrumb li ul.dropdown li {
  display: block;
  text-align: center;
  padding: 0;
}

.separador {
  padding: 0;
  margin: 0;
  color: #aaaaaa;
}

.activeMenuDrop {
  color: rgb(0, 51, 51);
}

.inactiveMenuDrop {
  color: #aaaaaa;
}

.inactiveMenuDrop:hover {
  color: rgb(0, 51, 51);
}

/* BACK TO MENU */

.resBack2 {
  display: none;
}

.goButtom {
  padding: 0.5em;
  margin-bottom: 2em;
  font-weight: 700;
  background-color: rgb(0, 51, 51);
  color: white;
  border-radius: 10px;
  transition: background-color 0.5s ease;
  transition: color 0.5s ease;
}

.goButtom:hover {
  color: rgb(0, 51, 51);
  background-color: white;
}

#envoltorio,
#envoltorioRes {
  float: left;
  position: relative;
  overflow: visible;
  /* para centrar el elemento */
  left: 50%;
}

#envoltorio2 {
  width: 100%;
  margin: auto;
  position: relative;
}

#clear {
  clear: left;
}

#back a,
#backRes a,
#back a:visited,
#backRes a:visited {
  padding: 0.5em;
  margin: auto;
  margin-bottom: 2em;
  font-weight: 700;
  text-align: center;
  float: left;
  position: relative;
  /* para centrar el elemento */
  left: -50%;
  display: block;
  background-color: rgb(0, 51, 51);
  color: white;
  text-decoration: none;
  border-radius: 0.5em;
  transition: background-color 0.5s ease;
  transition: color 0.5s ease;
}

#back a:hover,
#backRes a:hover {
  color: rgb(0, 51, 51);
  background-color: white;
}

/* ELEMENTOS - PROPIEDADES COMUNES */

main {
  padding-top: 4em;
}

/* títulos */
h1,
h2 {
  padding-left: 15%;
  font-family: "Georgia", serif;
  color: rgb(0, 51, 51);
}

.h3Text {
  display: block;
  margin: 1em 0 1em 0;
  font-weight: 800;
  font-size: 1.2em;
}

p {
  margin: 0;
}

img {
  /* para controlar mejor las dimensiones, así se adaptará al elemento padre*/
  width: 100%;
}

/* TXT */

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding-left: 0;
}

input {
  font-family: "Avenir", "Helvetica Neue", sans-serif;
  border: none;
}

.linkVisible {
  color: rgb(0, 51, 51);
  border-bottom: 2px solid #cccccc;
  transition: border-bottom 0.5s ease;
}

.linkVisible:hover {
  color: rgb(0, 51, 51);
  border-bottom: 2px solid rgb(0, 51, 51);
}

.linkVisibleTitle {
  color: rgb(0, 51, 51);
}

.linkVisibleTitle:hover {
  color: #cccccc;
}

.linkVisible2 {
  color: rgb(0, 51, 51);
  transition: color 0.5s ease;
}

.linkVisible2:hover {
  color: #cccccc;
}

.boldText {
  font-weight: 700;
}

.boldTextGreen {
  font-weight: 700;
  color: rgb(0, 51, 51);
}

.textGreen {
  color: rgb(0, 51, 51) !important;
}

/* OTROS */

.floatLeft {
  float: left;
}

.floatRight {
  float: right;
}

.elementBlock {
  display: block;
}

/* REJILLAS */

.texto {
  margin: 0 15% 0 15%;
  font-size: 1.3em;
  text-align: justify;
  hyphens: auto;
}

.texto2 {
  font-size: 1.3em;
  text-align: justify;
  hyphens: auto;
}

.texto p,
.texto2 p {
  margin-bottom: 1em;
}

/* REJILLA FLEXIBLE - padre */
.fatherGrid {
  display: flex;
  flex-wrap: wrap;
  clear: both;
  margin: 0 15% 0 15%;
}

/* REJILLA FLEXIBLE x2 */
.grid2Left {
  width: 45%;
  margin-right: 5%;
  padding-bottom: 1em;
}

.grid2Right {
  width: 45%;
  margin-left: 5%;
  padding-bottom: 1em;
}

/* REJILLA FLEXIBLE x3 */
.grid3Left {
  width: 30%;
  margin-right: 2.5%;
  padding-bottom: 1em;
}

.grid3Center {
  width: 30%;
  margin-left: 2.5%;
  margin-right: 2.5%;
  padding-bottom: 1em;
}

.grid3Right {
  width: 30%;
  margin-left: 2.5%;
  padding-bottom: 1em;
}

/* REJILLA FLEXIBLE sección visit - page index*/
.visitHour {
  width: 30%;
  margin-right: 20%;
  padding-bottom: 1em;
}

.visitPrice {
  width: 45%;
  margin-right: 5%;
  padding-bottom: 1em;
}

.grid70Left {
  width: 70%;
  margin-right: 30%;
}

.grid70Left h2 {
  margin-top: 0;
}

/* PAGE - exposiciones */

/* VIDEO */
.video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
}

.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* SECTION */

section {
  margin-bottom: 3em;
}

section input {
  /* para centrar el contenido */
  display: block;
  float: left;
  overflow: hidden;
  font-size: 1em;
}

::placeholder {
  color: #d0d0d0;
  font-size: 0.75em;
  padding-left: 0.25em;
}

/* DETAIL */
#section1Detail {
  background-image: url(../img/task.jpg);
  background-size: cover;
  min-height: 40vh;
  width: 100vw;
  margin: 0;
  margin-bottom: 2em;
  float: left;
  clear: both;
  color: white;
}

#section2Detail {
  background-image: url(../img/code.jpg);
  background-size: cover;
  min-height: 40vh;
  width: 100vw;
  margin: 0;
  margin-bottom: 2em;
  float: left;
  clear: both;
  color: white;
}

#section1Detail h1,
#section2Detail h1 {
  color: white;
}

.coverText {
  font-weight: 500;
  padding-left: 15%;
}

.transbox {
  background-color: rgba(0, 51, 51, 0.3);
  padding: 1em;
  min-height: 40vh;
}

.subTitle {
  font-size: 0.8em !important;
  font-style: italic;
  text-align: left;
}

/* FOOTER */

.titleFooter {
  padding-left: 0;
}

footer {
  background-color: #eae8e8;
  color: rgb(0, 51, 51);
  font-weight: 500;
  display: block;
  clear: both;
  text-align: center;
  padding: 2em;
}

.foot1 ul li {
  display: inline;
  padding: 0 1em 0 1em;
}

.colorIcon {
  transition: color 0.5s ease;
  color: rgb(0, 51, 51);
  font-size: 1.25em;
}

.colorIcon:hover {
  color: white;
}

.foot2 h3 {
  margin-bottom: 0;
}

.foot2 ul {
  margin-top: 0;
}

.foot2 ::placeholder {
  text-align: center;
}

.foot2 ul li label,
.foot2 ul li input {
  margin: auto;
  display: block;
  clear: both;
  padding: 0.5em 0;
}

#subscribe {
  font-size: 1em;
}

.foot2 {
  background-color: #cccccc;
  width: 600px;
  padding: 2px;
  margin: auto;
  border-radius: 1em;
}

.foot3 {
  margin-bottom: 2em;
}

.foot3 a {
  background-color: #cccccc;
  opacity: 0.8;
  padding: 0.5em;
  width: 20%;
  border-radius: 1em;
}

.foot3 ul li {
  margin-top: 0.5em;
}

.clip-svg {
  clip-path: url(#myClip);
  width: 50px;
}

/* MEDIA QUERIES */

@media only screen and (max-width: 1500px) {
  /* FONT a heredar */
  html {
    font-size: 16px;
  }
}
@media only screen and (max-width: 768px) {
  .texto {
    margin: 0 4% 0 4%;
  }

  /* TEXTO */
  h1,
  h2 {
    padding-left: 4%;
  }

  h1 {
    font-size: 1.5em;
  }

  /* REJILLAS para contenido */
  .fatherGrid {
    margin: 0 4% 0 4%;
  }

  .grid2Left,
  .grid2Right,
  .grid3Left,
  .grid3Center,
  .grid3Right {
    width: 100%;
    margin: 0%;
    padding-bottom: 3em;
  }

  /* NAV */
  nav ul li {
    display: block;
    clear: both;
  }

  #menu {
    display: none;
  }

  .breadcrumb > li > a {
    padding-top: 0;
  }

  .breadcrumb > li {
    display: block;
    width: auto;
    text-align: center;
  }

  .breadcrumb li ul.dropdown {
    width: 30%;
    margin: auto;
    left: 35%;
  }

  .dropdown li {
    width: 100%;
    text-align: center;
  }

  .menuRes li ul.dropdown {
    width: 40%;
    left: 30%;
  }

  .separador {
    display: none !important;
  }

  #navRes {
    display: block;
  }

  /* BACK MENU */

  .resBack1 {
    display: none;
  }

  .resBack2 {
    display: block;
  }

  /* PAGINAS */

  /* img COVER*/
  picture {
    width: 100%;
  }

  /* FOOTER - formulario */

  .foot2 {
    width: 230px;
    padding: 10px;
  }
}

@media only screen and (max-width: 375px) {
  /* NAV */
  /* menú de migas - breadcrumb */
  .breadcrumb li ul.dropdown {
    width: 50%;
    margin: auto;
    left: 25%;
  }

  .breadcrumb li a {
    font-size: 0.8em;
  }
}

@keyframes openYourMind {
  from {
    r: 0;
  }
  to {
    r: 400px;
  }
}

